body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
  background-color: grey;
}

ol, ul {
  padding-left: 30px;
}

.button {
}

div.gallery {
  display: flex-wrap;
  margin: 10px;
}

/*
div.gallery:hover {
  border: 1px solid #777;
}
*/

div.gallery img {
  width:400px;
  height:auto;
  margin: 10px;
  image-rendering: pixelated;
  image-rendering: -moz-crisp-edges;
  image-rendering: crisp-edges;
}

div.desc {
  padding: 15px;
  text-align: center;
}

.workDisplay {
  float: left;
  margin-right: 20px;
}

.bmpPreview {
  float: right;
  position: relative;
  margin-right: 20px;
}

.panelDisplay {
  /*float: center;*/
  margin: auto;
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 50px;
}

#previewComponent{
  position: absolute;
}

.workImage {
  width:200px;
  height:auto;
  image-rendering: pixelated;
  image-rendering: -moz-crisp-edges;
  image-rendering: crisp-edges;
}

.myButton {
  background-color: #1565c0;
  border: 2px solid #ffffff;
  color: white;
  padding: 5px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  margin: 10px 40px;
  cursor: pointer;
}

.myForm {
  background-color: #1565c0;
  border: 2px solid #ffffff;
  color: white;
  padding: 5px;
  text-align: center;
  display: inline-block;
  font-size: 16px;
  margin: 10px 40px;
  cursor: pointer;
}

.hide {
  display: none;
}
